/* Home 黄白主题 */
.homeContainer{
  background: var(--cream-bg);
  color: var(--deep-navy);
}

/* Hero */
.hero{ position: relative; height: 58vh; min-height: 420px; overflow: hidden; }
.heroImage{ width:100%; height:100%; object-fit: cover; display:block; }
.heroOverlay{ position:absolute; inset:0; background: linear-gradient(180deg, rgba(0,0,0,.35) 0%, rgba(0,0,0,.55) 60%, rgba(0,0,0,.65) 100%); }
.heroContent{ position:absolute; left:0; right:0; bottom:0; padding: 0 1.25rem 2rem; max-width: 1200px; margin: 0 auto; }
.welcomeKicker{ color:#fff; font-size:1.25rem; opacity:.95; }
.welcomeName{ color:#fff; font-size:3.2rem; margin:.25rem 0 .5rem; font-weight:700; letter-spacing:.5px; }
.welcomeSub{ color:#f0f0f0; font-size:1.1rem; margin-bottom:1.2rem; }
.heroActions{ display:flex; gap:1rem; }
.heroActions button:hover{
  color: var(--deep-navy) !important;
  border-color: var(--sand) !important;
}
.bookBtn{ background:#fff; color: var(--deep-navy); border:2px solid #fff; height:auto; padding:.9rem 1.4rem; border-radius:32px; font-weight:700; }
.inviteBtn{ background:transparent; color:#fff; border:2px solid #fff; height:auto; padding:.9rem 1.4rem; border-radius:32px; font-weight:700; }
.bookBtn:hover{ filter:brightness(.95); }
.inviteBtn:hover{ background:rgba(255,255,255,.08); }

/* notice card */
.noticeSection{ padding: 1.2rem; }
.noticeCard{ position:relative; background:#fff; border-radius:14px; border:1px solid var(--sand); box-shadow:0 6px 20px rgba(0,0,0,.06); padding:1.2rem 1.2rem 1rem; max-width:1200px; margin:0 auto; }
.noticeHeader{ font-size:1.4rem; font-weight:700; margin-bottom:.6rem; color:var(--deep-navy); }
.noticeBody{ color:#4b4b4b; line-height:1.7; }
.noticeFooter{ display:flex; justify-content:flex-end; margin-top:.8rem; }
.linkBtn{ background:transparent; color:var(--deep-navy); font-weight:700; border:none; cursor:pointer; }
.closeBtn{ position:absolute; right:.8rem; top:.8rem; width:32px; height:32px; border:none; background:#f5f5f5; border-radius:50%; cursor:pointer; color:#333; }

/* Upcoming */
.upcomingSection{ padding: .5rem 1.2rem 2.5rem; max-width:1200px; margin:0 auto; }
.upcomingHeader{ display:flex; align-items:center; justify-content:space-between; margin:1.4rem 0 .8rem; }
.upcomingTitle{ font-size:1.8rem; font-weight:800; color:var(--deep-navy); }
.linkGhost{ background:transparent; border:none; color:var(--deep-navy); font-weight:700; cursor:pointer; }

.upcomingList{ display:grid; grid-template-columns: 1fr; gap:1.2rem; }
.upcomingItem{ display:flex; align-items:center; gap:1rem; background:#fff; border:1px solid var(--sand); border-radius:12px; padding:.8rem; }
.upImgWrap{ width:84px; height:84px; border-radius:10px; overflow:hidden; flex-shrink:0; }
.upImgWrap img{ width:100%; height:100%; object-fit:cover; display:block; }
.upInfo{ min-width:0; }
.upTitle{ font-weight:800; font-size:1.2rem; color:var(--deep-navy); }
.upMeta{ color:#5a5a5a; margin-top:.1rem; }
.upTime{ color:#6c6c6c; margin-top:.1rem; }

/* Desktop adjustments */
@media (min-width: 900px){
  .hero{ height: 68vh; }
  .heroContent{ padding: 0 2rem 2.2rem; }
  .upcomingList{ grid-template-columns: 1fr 1fr; }
  .upcomingItem{ padding:1rem; }
  .upImgWrap{ width:96px; height:96px; }
}

/* Small screens */
@media (max-width: 480px){
  .welcomeName{ font-size:2.4rem; }
  .welcomeSub{ font-size:1rem; }
  .heroActions{ gap:.75rem; }
  .bookBtn,.inviteBtn{ padding:.7rem 1.1rem; }
}
